@import url(https://fonts.googleapis.com/css?family=Muli);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.koshurvagabond-views-login-form{

    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 60px;
}

.koshurvagabond-views-formitem{

    min-width: 70%;
}

.koshurvagabond-views-login{

    margin-top: 20px;
}

.koshurvagabond-views-login-socialmedia{

    margin-top: 50px;
}

*{
    font-family: 'Muli', sans-serif;
}

.app-container{

    display: flex;
    flex-direction: column;
    background-color: #82D670;
    height: 100%;

    position:fixed;
    top:0px;
    bottom:0px;
    left:0px;
    right:0px;
}

.koshurvagabond-form-input{

    border-radius: 6px;
    margin-top: 10px;
    padding: 6px;
    min-height: 30px;
    font-size: 18px;
    
}

.koshurvagabond-form-submit{

   
    background-color: #1D640F;
    border-color: #1D640F;
    color: antiquewhite;
}

.koshurvagabond-title-text{

    color: #1D640F;
}

.koshurvagabond-link{

    margin-top:20px;
    font-size: 18px;
}

.koshurvagabond-views-loading-container{

    display: flex;
    flex-direction: column;
    height: 100%;

    position:fixed;
    top:0px;
    bottom:0px;
    left:0px;
    right:0px;
    background-image:url(../../static/media/loading.e4be12bb.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
